.item__name {
  max-width: 204px;
  margin-bottom: 17px;
  font-family: "CormorantInfant-Bold", "Arial", sans-serif;
  font-size: 40px;
  line-height: 48px;
  color: #195557; }

.item__desc {
  max-width: 182px;
  margin-bottom: 7px;
  font-family: "Raleway-Regular", "Arial", sans-serif;
  font-size: 14px;
  line-height: 22px;
  color: #000000; }

.item__price {
  display: flex;
  flex-direction: column;
  align-items: flex-start; }
  .item__price-old {
    font-size: 20px;
    line-height: 24px;
    text-decoration: line-through;
    color: #c2ccca; }
  .item__price-new {
    font-size: 40px;
    line-height: 48px;
    color: #195557; }

.item-picture {
  margin-top: 45px; }
  .item-picture__img {
    width: 363px;
    height: 265px; }
    .item-picture__img--maks {
      transform: rotate(-1.96deg); }

.item__plus {
  position: absolute;
  bottom: 0;
  right: 0;
  bottom: 0;
  width: 40px;
  height: 40px;
  z-index: 10;
  text-align: center; }
  .item__plus-img {
    width: 27px;
    height: 27px; }

